@import './mixins/transition';

.ee-input-base-hover {
	border-color: var(--ee-color-primary-low-contrast);
	color: var(--ee-color-primary-high-contrast);
	box-shadow: none;
}

.ee-input-base-focus {
	border-color: var(--ee-color-primary);
	box-shadow: none;
	/* Visible in Windows high-contrast themes */
	outline-color: transparent;
	outline-width: var(--ee-border-width);
	outline-style: solid;
}

.ee-base-inputs-styles {
	background: var(--ee-background-color);
	border-color: var(--ee-border-color);
	border-radius: var(--ee-border-radius-small);
	border-style: solid;
	border-width: var(--ee-border-width);
	box-shadow: none;
	color: var(--ee-default-text-color);
	font-size: var(--ee-font-size-default);
	line-height: inherit;
	min-height: var(--ee-icon-button-size); // 42px
	outline: none;
	padding: var(--ee-padding-micro) var(--ee-padding-tiny);
	@include transition(all ease 50ms);
}

.ee-input-base.ee-input-base,
.ee-input__wrapper .ee-input-base {
	@extend .ee-base-inputs-styles;

	&.ee-input,
	&.ee-select,
	&.ee-textarea {
		@at-root {
			select#{&},
			input[type='text']#{&},
			input[type='password']#{&},
			input[type='date']#{&},
			input[type='datetime']#{&},
			input[type='datetime-local']#{&},
			input[type='email']#{&},
			input[type='month']#{&},
			input[type='number']#{&},
			input[type='search']#{&},
			input[type='tel']#{&},
			input[type='text']#{&},
			input[type='time']#{&},
			input[type='url']#{&},
			input[type='week']#{&} {
				@extend .ee-base-inputs-styles;

				&:focus {
					@extend .ee-input-base-focus;
				}
				&:hover {
					@extend .ee-input-base-hover;
				}
			}
		}

		&.ee-select {
			line-height: 1.5;
			padding: var(--ee-padding-micro) var(--ee-padding-default) var(--ee-padding-micro) var(--ee-padding-smaller);

			[dir='rtl'] & {
				padding: var(--ee-padding-micro) var(--ee-padding-smaller) var(--ee-padding-micro)
					var(--ee-padding-default);
			}
		}

		&:focus {
			@extend .ee-input-base-focus;
		}
		&:hover {
			@extend .ee-input-base-hover;
		}
		&:disabled {
			background: var(--ee-color-grey-14);
			border-color: var(--ee-color-grey-11);
			color: var(--ee-default-text-color-low-contrast);
			cursor: not-allowed;
			opacity: 0.8;

			&:hover {
				border-color: var(--ee-color-grey-9);
				color: var(--ee-default-text-color-low-contrast);
			}
		}
	}
}
